import uniLoadMore from '@/components/uni/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty"; 
export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			now_pay:0,
			now_drive:0,	
			now_pinjia:0,
			getimg:this.$getimg,
			tabCurrentIndex: 0,
			order_list: '',
			navList: [{
					state: 0,
					text: '全部',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 1,
					text: '待付款',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 2,
					text: '待收货',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 3,
					text: '待评价',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 4,
					text: '售后',
					loadingType: 'more',
					orderList: []
				}
			],
		};
	},

	onLoad(options) {
		this.get_order()
		/**
		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
		 * 替换onLoad下代码即可
		 */
		this.tabCurrentIndex = +options.state;




		// if (options.state == 0) {
		// 	this.loadData()
		// }


	},

	methods: {
		jumo_tomyorder(id) {
			uni.navigateTo({
				url: '../user/myorder/myorder?id=' + id
			})
		},
		get_order() {
			const that = this
			this.$api.http.post('order/user/all_order').then(res => {
				this.order_list = res.data					 
				console.log(this.tabCurrentIndex)	
			})
		},
		//支付
		async pay_again(id){
			const pay_data=await this.$api.http.post('order/pay/pre_order', {
				id: id
			}).then(res => {
				console.log('pay:', res)
				return res
			})				
			await this.onpay(pay_data)  
		},
		//支付
		onpay(data) {  
			const that=this
		    uni.requestPayment({
		    	provider:"wxpay", 
		    	timeStamp: data.timeStamp,
		    	nonceStr: data.nonceStr,
		    	package: data.package,
		    	signType: data.signType,
		    	paySign: data.paySign,
		    	success: function (res) {  
		    		console.log('success:' + JSON.stringify(res)); 
		    		that.get_order()
		    	},
		    	fail: function (err) { 						
		    		console.log('fail:' + JSON.stringify(err)); 
		    	}
		    });
		},			
		
		//获取订单列表
		loadData(source) {},

		//swiper 切换
		changeTab(e) {
			this.tabClick(e.target.current)
		},
		//顶部tab点击
		tabClick(index) {
			this.tabCurrentIndex = index;
			if(index==1){
				this.now_pay=0
				this.now_drive=0		
				this.now_pinjia=0
			}
			if(index==2){
				this.now_pay=1	
				this.now_drive=0		
				this.now_pinjia=0
			}
			if(index==3){		
				this.now_pay=1				
				this.now_drive=1		
				this.now_pinjia=0
			} 
			if(index==4){
				this.now_pay=1				
				this.now_drive=1			
				this.now_pinjia=2
			} 
		},
		//删除订单
		deleteOrder(id) {
			const that = this
			uni.showModal({
			    title: '提示',
			    content: '确定删除订单？',
			    success: function (res) {
			        if (res.confirm) {
			            console.log('用户点击确定');
						that.$api.http.put('order/user/del_order?id='+id).then(res=>{
							uni.showToast({
							    title: '删除成功',
							    duration: 2000
							});
							that.get_order()
						})
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});	 
		},

		//订单状态文字和颜色
		orderStateExp(state) {
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch (+state) {
				case 1:
					stateTip = '待付款';
					break;
				case 2:
					stateTip = '待发货';
					break;
				case 9:
					stateTip = '订单已关闭';
					stateTipColor = '#909399';
					break;

					//更多自定义
			}
			return {
				stateTip,
				stateTipColor
			};
		}
	},
	onPullDownRefresh() {
		this.get_order()
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 2000);
	}
}
